}
.circled {
  font-family: 'Open Sans', HelveticaNeue, Helvetica, Arial, sans-serif;
  font-size: 32px;
  font-weight: 100;
  text-align: center;
  border-radius: 100%;
  color: white;
  background-color: @brand-primary;
  margin: 20px auto;
  width: 62px;
  height: 62px;
  line-height: 62px;
}
.circled + .text-uppercase {
  margin-bottom: (@line-height-computed*1.5);
}
.large-circled {
  font-family: 'Open Sans', HelveticaNeue, Helvetica, Arial, sans-serif;
  // font-size: 32px;
  font-weight: 100;
  text-align: center;
  border-radius: 100%;
  color: white;
  background-color: @brand-primary;
  margin: 20px auto;
  padding-top: 12px;
  width: 160px;
  height: 160px;
  text-transform: uppercase;
  line-height: 1;
  // line-height: 62px;
}
.large-circled .text-uppercase {
  font-weight: bold;
  font-size: 30px;
}
.large-circled .value {
  font-family: 'Raleway', serif;
  font-size: 100px;
  font-weight: 900;
  line-height: .6;
}
footer.footer {
  border-top: 1px solid @gray-lighter;
  margin: (@line-height-computed) 0 0;
  padding: 10px 0 70px;
  position: relative;
}

// login form
#login {
  background-color: @brand-primary;
  color: @gray;
  position: relative;
}

#login .panel {
  background-color: #f2f2f2;
  color: @text-color;
  margin-top: (@line-height-computed*4);
  padding: @line-height-computed 0;
}
#login .panel .panel-heading {
  background-color: transparent;
  background-image: none;
  border: 0;
}
#login .ribbon {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
#login footer.footer {
  border-top: 0;
  color: @gray;
}

// dashboard
a[title="JavaScript charts"] {
  display: none !important;
}

// floating widget for my account quicklinks
.floater-holder {
  position: relative;
}
.floating-widget {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1000;
  background-color: #f2f2f2;
  text-align: center;
  margin: 0 auto (@line-height-computed);
}
.floating-widget a {
  color: #454545;
  text-decoration: underline;
}
.floating-widget .widget-title {
  background-color: @brand-primary;
  color: white;
  font-size: 1.2em;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 5px;
  padding: 5px 20px;
}
@media screen and (max-width: @screen-xs-max){
  .floating-widget {
    position: relative;
    margin-left: 15px;
    margin-right: 15px;
    // width: 95%;
    // margin: auto;
    padding-bottom: (@line-height-computed/4);
  }
}


// accordion
.panel-group {
  .panel {
    border: 0;
    box-shadow: none;
  }
  .panel-heading {
    border-radius: 3px;
    box-shadow: 0 1px 0 #e7e7e7;
  }
  .panel-heading + .panel-collapse > .panel-body {
    border: 0;
  }
}
.panel a {
  text-decoration: underline;
}
.panel-group .panel {
  color: lighten(@text-color, 25%);
  line-height: (@line-height-computed*1.3);

  i.indicator {
    margin-right: 5px;
  }
  &.panel-default i.indicator {
    color: #a0a0a0;
  }
  a {
    color: #a0a0a0;
    text-decoration: none;

    &:hover {
      color: @link-color;
    }
  }
}

