}
tr.new td:first-child{
  position: relative;
}
tr.new td:first-child:before {
  position: absolute;
  top: 50%;
  left: -(@font-size-base);
  display: block;
  width: @font-size-base;
  height: @line-height-computed;
  content: "●";
  color: @brand-primary;
  font-size: xx-large;
  line-height: .125;
  z-index: 10;
}

